  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptmonoregular.woff2") format("woff2"), url("../fonts/ptmonoregular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/robotomedium.woff2") format("woff2"), url("../fonts/robotomedium.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/robotocondensedregular.woff2") format("woff2"), url("../fonts/robotocondensedregular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
  font-display: swap; }

img {
  max-width: 100%;
  object-fit: contain; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  height: 100%;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
  color: #041827;
  background: #f8fdff;
  min-width: 320px;
  scroll-behavior: smooth; }
  @media (min-width: 320px) {
    .page {
      overflow-x: hidden; } }

.page__body {
  height: 100%;
  margin: 0;
  position: relative; }
  @media (min-width: 320px) {
    .page__body {
      overflow-x: hidden; } }

.page__wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column; }

.page__header {
  position: fixed;
  width: 100%;
  z-index: 2;
  background: linear-gradient(#041827 84.9%, rgba(4, 24, 39, 0) 100%); }

.page__main {
  flex: 1 1 auto; }

.nav {
  width: 1220px;
  margin: 0 auto; }

.nav__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 60px;
  min-height: 120px; }

.nav__link {
  display: grid;
  grid-template-columns: 55px 1fr;
  width: 215px;
  gap: 15px;
  text-decoration: none;
  color: #f2f5f6;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  align-items: center; }

.nav__link--active {
  pointer-events: none; }

.nav__link span {
  display: block; }

.nav__link-description {
  color: rgba(255, 255, 255, 0.7);
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500; }

.nav__button {
  background: transparent;
  color: #f2f5f6;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  text-transform: uppercase;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 700;
  padding: 10px 15px;
  cursor: pointer; }

.contacts {
  width: 480px; }

.contacts__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  line-height: 18px;
  font-style: normal; }

.contacts__details {
  color: #f2f5f6;
  text-decoration: none;
  position: relative;
  display: block;
  padding: 10px 10px 10px 30px;
  box-sizing: border-box; }

.contacts__icon {
  position: absolute;
  top: 7px;
  left: 0; }

.intro {
  background: #041827;
  box-sizing: border-box;
  background-image: url("../img/background/intro/back.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-bottom: 120px; }
  @media (min-resolution: 2dppx) {
    .intro {
      background-image: url("../img/background/intro/back@2x.jpg"); } }

.intro__wrapper {
  min-height: 880px;
  padding-top: 120px;
  position: relative; }

.intro__wrapper::after {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background: linear-gradient(#192024 71.72%, rgba(25, 32, 36, 0) 100%);
  opacity: 0.7; }

.intro__description {
  color: #f2f5f6;
  position: relative;
  z-index: 1;
  margin: 0 auto;
  margin-top: 185px;
  width: 970px; }

.intro__description h1 {
  margin: 0 0 40px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase; }

.intro__description p {
  margin: 0 0 40px;
  font-size: 22px;
  line-height: 34px;
  width: 685px; }

.intro__link {
  display: block;
  color: #f2f5f6;
  text-decoration: none;
  background: #00b2ff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 15px 25px;
  box-sizing: border-box;
  border-radius: 3px;
  width: 410px; }

.benefits {
  width: 1222px;
  margin: 0 auto 120px; }

.benefits__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px; }

.benefits__item {
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
  position: relative;
  padding: 95px 45px 35px;
  box-sizing: border-box; }

.benefits__icon {
  position: absolute;
  top: 0;
  left: 0;
  fill: #00b2ff; }

.benefits__item h3 {
  margin: 0 0 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;
  color: #00b2ff; }

.benefits__item p {
  margin: 0; }

.benefits__item:hover {
  cursor: pointer;
  background: #00b2ff;
  border-color: #00b2ff; }

.benefits__item:hover .benefits__icon {
  fill: #f2f5f6; }

.benefits__item:hover h3,
.benefits__item:hover p {
  color: #f2f5f6; }

.about {
  width: 1220px;
  margin: 0 auto 100px;
  display: grid;
  grid-template-columns: 150px 1fr;
  gap: 100px; }

.about__wrapper h2 {
  margin: 0 0 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px; }

.about__list {
  list-style-type: none;
  margin: 0 0 20px;
  padding: 0; }

.about__item p {
  margin: 0 0 15px; }

.about__button {
  cursor: pointer;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 700;
  background: transparent;
  border-radius: 3px;
  color: #00b2ff;
  text-transform: uppercase;
  border: 1px solid #00b2ff;
  padding: 14px 12px;
  width: 180px; }

.catalog {
  background: #041827;
  padding: 70px 0 140px; }

.catalog__wrapper {
  width: 1220px;
  margin: 0 auto; }

.catalog__wrapper h2 {
  color: #ffffff;
  margin: 0 0 50px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400; }

.catalog__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 85px; }

.card {
  position: relative;
  display: block;
  padding: 50px 0 0 40px;
  box-sizing: border-box;
  min-height: 650px; }

.card__link {
  color: #ffffff;
  text-decoration: none;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 20px;
  line-height: 30px;
  display: block; }

.card__link h3 {
  position: relative;
  width: 200px;
  margin: 0;
  font-weight: 400; }

.card__picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  background: #041827; }

.card__picture::after {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background: rgba(4, 24, 39, 0.5); }

.card__picture img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover; }

.card__icon {
  position: absolute;
  bottom: 0;
  right: 0;
  fill: transparent; }

.feedback {
  background: #00b3ff;
  background-image: url("../img/svg/polygon.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 170px 0 250px; }

.feedback__wrapper {
  width: 1220px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 510px 1fr;
  gap: 120px;
  align-items: center; }

.form {
  color: #ffffff; }

.form__fieldset {
  border: none;
  padding: 0;
  margin: 0; }

.form__fieldset legend {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 36px;
  line-height: 51px; }

.form__fieldset p {
  margin: 0 0 35px; }

.form__list {
  list-style-type: none;
  margin: 0 0 35px;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px; }

.form__item:first-child {
  margin-right: 10px; }

.form__item--question {
  grid-column: 1 / 3; }

.form__item input,
.form__question {
  width: 100%;
  font-size: 16px;
  line-height: 21px;
  padding: 15px;
  box-sizing: border-box;
  border: none;
  border-radius: 3px;
  background: rgba(4, 20, 39, 0.15);
  color: #ffffff; }

.form__question {
  padding: 25px 15px;
  resize: none;
  min-height: 155px; }

.form__item input::placeholder,
.form__question::placeholder {
  color: #ffffff; }

.form__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  align-items: center; }

.form__wrapper button {
  background: #041427;
  cursor: pointer;
  border: none;
  border-radius: 3px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 19px;
  font-weight: 700;
  color: #ffffff;
  padding: 15px 25px;
  box-sizing: border-box; }

.control {
  position: relative;
  cursor: pointer;
  padding-left: 23px; }

.control__mark-icon {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 9px;
  height: 6px;
  content: "";
  z-index: 2;
  fill: #f8fdff;
  fill: transparent; }

.control__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  border-radius: 2px;
  border: 1px solid #ffffff;
  content: ""; }

.control span:last-child {
  font-size: 14px;
  line-height: 16px; }

.control input[type="checkbox"]:checked + .control__mark-icon {
  display: block; }

.footer {
  background: #041827;
  padding: 50px 0 55px;
  height: 280px;
  box-sizing: border-box; }

.footer__wrapper {
  width: 1220px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.footer__link {
  display: grid;
  grid-template-columns: 55px 1fr;
  width: 204px;
  gap: 15px;
  text-decoration: none;
  color: #f2f5f6;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  align-items: center; }

.footer__link--active {
  pointer-events: none; }

.footer__link span {
  display: block; }

.footer__link-description {
  color: rgba(248, 253, 255, 0.7);
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500; }

.external {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.chapter {
  color: #ffffff; }

.chapter h2 {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px; }

.chapter__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 90px; }

.chapter__item-link {
  display: block;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.7); }

.footer-contacts {
  color: #ffffff;
  font-style: normal; }

.footer-contacts h2 {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px; }

.footer-contacts__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 90px; }

.footer-contacts__details {
  display: block;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.7); }
